



import { AddRfqComponent } from './add-rfq/add-rfq.component';


import { RfqManagmentComponent } from './rfq-managment.component';
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { EditRfqComponent } from './edit-rfq/edit-rfq.component';
import { SmartTableComponent } from './consult-rfq/smart-table.component';
import { rfqservice } from './rfq-service';
import { AuthGuard } from '../../auth/auth.gards';


const routes: Routes = [{
  path: '',
  component: RfqManagmentComponent,
  children: [
    {
      path: 'addRfq',
      component: AddRfqComponent,canActivate: [AuthGuard]
    },
    {
      path: 'editRfq/:id',
      component: EditRfqComponent,canActivate: [AuthGuard]
    },
   
   
    {
      path: 'smart-table',
      component: SmartTableComponent,canActivate: [AuthGuard]    },
  ],
}];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [
    RouterModule
  ],
})
export class RfqManagmentRoutingModule {

}

export const routedComponents = [
  RfqManagmentComponent,
  AddRfqComponent,
  EditRfqComponent,
  SmartTableComponent,
  RfqManagmentComponent
];
