import { Subscription } from "rxjs";
import { HttpClient } from "@angular/common/http";
import { NbDateService, NbThemeService } from "@nebular/theme";
import { Component } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";




@Component({
  selector: 'ngx-edit-rfq',
  styleUrls: ['./edit-rfq.component.scss'],
  templateUrl: './edit-rfq.component.html',
})
export class EditRfqComponent {
  starRate = 2;
  heartRate = 4;
  radioGroupValue = 'This is value 2';
  min: Date;
  max: Date;
customer:string;

segment: String;
ProjectName: String;
BuyerName: String;
BuyerTel: String;
BuyerAdress: String;
RFQName: String;
RFQNumber: String;
SalesManagerInchagre: String;
faisabilityStudyInCharge: String
NumberOfParts: String;
RD: String;
PlantToDeliverTo: String;
  themeName = 'default';
  settings: Array<any>;
  id:string;
  themeSubscription: Subscription;
  constructor(private http:HttpClient,private router: Router,private route: ActivatedRoute, protected dateService: NbDateService<Date>,private themeService: NbThemeService) {
    this.min = this.dateService.addDay(this.dateService.today(), -5);
    this.max = this.dateService.addDay(this.dateService.today(), 5);
    this.themeSubscription = this.themeService.getJsTheme().subscribe(theme => {
      this.themeName = theme.name;
      this.init(theme.variables);});
      this.route.params.subscribe(params=>{this.id=params['id'];
      this.http.get<any>('http://localhost:3000/rfq/'+this.id).subscribe(result=>{
     this.customer= result.customer;
    
     this.segment= result.segment
     this.ProjectName= result.ProjectName
     this.BuyerName= result.BuyerName
     this.BuyerTel= result.BuyerTel
     this.BuyerAdress= result.BuyerAdress
     this.RFQName= result.RFQName
     this.RFQNumber= result.RFQNumber
     this.SalesManagerInchagre= result.SalesManagerInchagre
     this.faisabilityStudyInCharge= result.faisabilityStudyInCharge
     this.NumberOfParts= result.NumberOfParts
     this.RD= result.RD
     this.PlantToDeliverTo= result.PlantToDeliverTo
     
      }
      )
      }
      );
      
    

  }
  EditRfq(rfqData)
  {   
    //let data={username: rfqData.customer, feedback: rfqData.segment};
    this.http.put<any>('http://localhost:3000/rfq/'+this.id, rfqData).subscribe(result=>{
    this.http.get<any>('http://localhost:3000/rfq').subscribe(result=>{
      this.router.navigate(['pages/rfq-managment-elements/smart-table']);
    });
    });
   
    
    
  }
  
 
  init(colors: any) {
    this.settings = [
      {
        status: 'primary',
        container: 'primary-container',
        title: 'Primary Button',
        buttonTitle: 'Save',
        default: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
        },
        corporate: {
          color: colors.primary,
          glow: {
            params: '0 0 20px 0',
            color: 'rgba (115, 161, 255, 0.5)',
          },
        },
        cosmic: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
          bevel: `shade(${colors.primary}, 14%)`,
          shadow: 'rgba (6, 7, 64, 0.5)',
          glow: {
            params: '0 2px 12px 0',
            color: `adjust-hue(${colors.primary}, 10deg)`,
          },
        },
      },
      {
        status: 'primary',
        container: 'primary-container',
        title: 'Primary Button',
        buttonTitle: 'Print',
        default: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
        },
        corporate: {
          color: colors.primary,
          glow: {
            params: '0 0 20px 0',
            color: 'rgba (115, 161, 255, 0.5)',
          },
        },
        cosmic: {
          gradientLeft: `adjust-hue(${colors.primary}, 20deg)`,
          gradientRight: colors.primary,
          bevel: `shade(${colors.primary}, 14%)`,
          shadow: 'rgba (6, 7, 64, 0.5)',
          glow: {
            params: '0 2px 12px 0',
            color: `adjust-hue(${colors.primary}, 10deg)`,
          },
        },
      },
    ];
  }
}
